@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

 :root {
  font-family: $font-primary;
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
  color: #191559;
  @include rfs(24);
}

.contr {
  width: 100%;
  max-width: 1615px;
  margin: 0 auto;
}

a {
  transition: all 0.3s ease;
  color: inherit;
  &:hover {
    text-decoration: none;
  }
}

p {
  margin: 0;
}

.button {
  padding: 1em;
  border: none;
  color: #fff;
  outline: none;
  @include rfs(18);
  font-weight: 900;
  line-height: 1.39;
  letter-spacing: normal;
  text-align: center;
  background-color: #ffa861;
  border-radius: 0;
  &:hover {
    color: #ffffff;
    background-color: darken(#ffa861, 5%);
  }
}

.button--wide {
  padding: 1em 3em;
  @include rfs(24);
}

.img-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  object-fit: cover;
  object-position: center;
}

.color-orange {
  color: #ffa861;
}

// HEADER
.header {
  padding: 46px 0 0;
  @media only screen and (max-width: 1600px) {
    padding-top: 2.4vw;
  }
  &__logo {
    @include rfs(20);
    .icon-main-logo {
      width: 74.6px;
      height: 84.7px;
    }
  }
  .main-menu {
    align-items: center;
    @media only screen and (max-width: 992px) {
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
    }
    .nav-item {
      &:not(:last-child) {
        margin-right: 2.5em;
        /* Large Devices, Wide Screens */
        @media only screen and (max-width: 1200px) {
          margin-right: 1em;
        }
      }
      &.active .nav-link {
        border-color: #ffffff;
        &:hover {
          border-color: #ffa861;
        }
      }
      .dropdown-toggle::after {
        display: none;
      }
    }
    .nav-link {
      font-size: 1em;
      padding: 0;
      color: #ffffff;
      transition: all 0.3s ease;
      cursor: pointer;
      border-bottom: solid 3px transparent;
      &:hover {
        color: #ffa861;
        border-color: #ffa861;
      }
      &.button {
        width: 155.2px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          color: #ffffff;
        }
      }
    }
    .dropdown-menu {
      min-width: 10rem;
      padding: 0;
      margin: 0;
      font-size: 1em;
      border: none;
      border-radius: 0;
      box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1), 0 8px 15px 0 rgba(0, 0, 0, 0.1);
    }
    .dropdown-item {
      color: #1bafff;
      padding: .25rem 0.6em;
      &:hover {
        color: #1476d1;
        background: transparent;
      }
      &:active {
        color: #ffffff;
        background-color: lighten(#1bafff, 10%);
      }
    }
  }
  .navbar-toggler {
    border: none;
    color: #fff;
    &:focus {
      outline: none!important;
    }
  }
  .navbar-toggler-icon {
    width: 2em;
    height: 2em;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(255,255,255)' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    @media only screen and (min-width: 480px) {
      margin-bottom: 0.7rem;
    }
  }
}

// Hero
.hero {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 2em;
  @include rfs(24);
  color: #ffffff;
  // background-color: #1c0c3b;
  @media only screen and (max-width: 768px) {
    @include rfs(18);
  }
  .container-fluid {
    z-index: 2;
  }
  .cont {
    max-width: 1720px;
  }
  &__text {
    max-width: 920px;
    margin: 0 auto;
    padding: 10vw 1rem 6.5vw;
  }
  &__title {
    font-family: 'Bebas Neue', sans-serif;
    font-size: 5em;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: 0.05em;
    @media only screen and (max-width: 480px) {
      font-size: 4em;
    }
  }
  &__subtitle {
    font-size: 1.25em;
    font-weight: 300;
    line-height: 1.37;
  }
  .button--app {
    margin: 5.5vw auto 0;
  }
  // bg carousel
  .hero__bg-carousel {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .carousel-item,
  .carousel-inner {
    height: 100%;
  }
  .hero__bg-slide {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.hero--low {
  min-height: 30vw;
  .hero__text {
    padding: 4vw 0 1vw;
  }
}

.hero--left {
  .hero__text {
    max-width: 1600px;
    .hero__subtitle {
      max-width: 875px;
    }
    @media only screen and (max-width: 992px) {
      text-align: center;
    }
  }
}

.bnt-scroll-to {
  .icon-chevron_down:hover {
    stroke: #ffa861;
  }
}

.icon-chevron_down {
  fill: none;
  stroke: #fff;
  stroke-width: 3px;
}

// FOOTER
.footer {
  background-color: rgba(226, 226, 226, 0.8);
  .footer__nav {
    padding: 2em 0 1em;
    max-width: 992px;
    margin: 0 auto;
    ul {
      padding-left: 0;
    }
    .icon {
      color: #ffffff;
      &:hover {
        color: #1bafff;
      }
    }
  }
  .nav-column {
    @media only screen and (max-width: 1200px) {
      margin: 0 auto;
      max-width: 260px;
    }
  }
  .nav-item {
    list-style-type: none;
  }
  .nav-title {
    font-size: 1.33em;
    color: #191559;
    font-family: 'Bebas Neue', sans-serif;
    text-transform: uppercase;
  }
  .nav-link {
    padding: 0 0;
    line-height: 1.83;
    font-size: 0.75;
    line-height: 1.67;
    color: #191559;
    &:hover {
      color: #ffa861;
    }
  }
}

// FAQ
.faq {
  .contr {
    max-width: 1075px;
  }
}

.still-have-question {
  padding: 1.7vw 0 5.3vw 0.83em;
  .content__title {
    font-family: $font-primary;
    text-transform: none!important;
    margin-bottom: 0.1em;
  }
  .button {
    margin-top: 1.5em;
    padding-left: 4em;
    padding-right: 4em;
  }
}

// Privacy
.privacy {
  padding: 5.73vw 0 3vw;
}

.article {
  padding: 0 1em;
  &__title {
    font-weight: 900;
  }
  p {
    margin-bottom: 1.5em;
  }
}

// accordion
.accordion {
  margin: 5.8vw 0;
  border-top: solid 1px #8e8d8f;
  .card {
    border: none;
  }
  .card-header {
    padding: 1.15em 0 0.82em;
    background-color: transparent;
    border-bottom: solid 1px #8e8d8f;
    .triangle-right {
      margin-right: 3.3vw;
      transition: transform 0.3s ease;
    }
    .icon-chevron_right {
      fill: none;
      stroke: #707070;
      stroke-width: 3px;
      @include rfs(29);
      width: 11px;
    }
    .btn[aria-expanded="true"] {
      .triangle-right {
        transform: rotate(90deg);
      }
    }
    .btn {
      display: flex;
      align-items: center;
      padding: 0;
      @include rfs(24);
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.38;
      letter-spacing: normal;
      text-align: left;
      white-space: initial;
      color: #442a59;
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }
  .card-body {
    padding-left: 0;
    padding-right: 0;
  }
}

.section-white {
  padding: 70px 0 0;
  @include rfs(24);
  text-align: center;
  .contr {
    max-width: 1260px;
  }
  &__title {
    text-transform: uppercase;
    font-size: 1.66em;
    font-weight: 900;
  }
}

// Mockup section
.mockup {
  margin-top: 2em;
  line-height: 1.37;
  @media only screen and (max-width: 1200px) {
    @include rfs(18);
  }
  &__row {
    display: flex;
    justify-content: space-around;
    width: 100%;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 393px;
    margin-bottom: 1em;
    img {
      margin: 0;
      max-width: 13.3em;
    }
  }
  &__name {
    padding: 0 0.5em;
    font-size: 1.25em;
    @media only screen and (min-width: 768px) {
      min-height: 3em;
    }
  }
  &__descr {
    padding: 1em 1em;
    font-size: 1em;
  }
}

// section improve
.section-improve {
  padding: 5vw 0;
  .section__subtitle {
    font-size: 1.25em;
  }
  .improve {
    margin-top: 4em;
    text-align: left;
    @include rfs(18);
    &__item {
      margin-bottom: 4em;
      @media only screen and (max-width: 580px) {
        text-align: center;
      }
    }
    &__icon {
      margin-bottom: 10px;
      @include rfs(82);
    }
    .icon {
      fill: #191559;
    }
    .icon-bandz {
      @include rfs(56);
    }
  }
}

.content {
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__column {
    position: relative;
  }
  &__title {
    position: relative;
    font-family: "Bebas Neue", sans-serif;
    @include rfs(70);
    text-transform: uppercase;
    font-weight: normal;
    line-height: 0.85;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: -0.33em;
      display: inline-block;
      width: 0.11em;
      height: 87%;
      background-color: #ffa861;
    }
  }
}

.section__we-serve {
  .content__column {
    &:first-child {
      flex-basis: 42%;
    }
    &:last-child {
      flex-basis: 58%;
    }
    @media only screen and (max-width: 1200px) {
      .we-serve__moto {
        padding: 3.33em 5vw;
      }
      .we-serve__mototext {
        padding: 0.5em 0.5em 2em 0.75em;
      }
    }
    @media only screen and (max-width: 850px) {
      &:first-child,
      &:last-child {
        flex-basis: 100%
      }
    }
  }
  .we-serve__moto {
    position: relative;
    height: 100%;
    padding: 3.33em;
    color: #ffffff;
    font-family: "Bebas Neue", sans-serif;
    @include rfs(28);
    line-height: 1.18;
    .img-bg {
      z-index: -1;
    }
  }
  .we-serve__mototext {
    max-width: 320px;
    margin-left: auto;
    padding: 0.5em 2em 3em 0.75em;
    border: solid 7px #ffffff;
  }
  .content__title {
    margin-bottom: 1em;
  }
}

// begin Tiles
.tiles-wrap {
  height: 100%;
  padding: 68px 6.5vw;
  background-color: #e7e7e7;
}

.tiles {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 746px;
}

.tiles__block {
  flex-basis: 32.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  min-height: 150px;
  margin: 5px 0;
  padding: 1em 0.5em 0.9em;
  font-family: "Bebas Neue", sans-serif;
  @include rfs(40);
  line-height: 1.13;
  letter-spacing: normal;
  text-align: center;
  color: #191559;
  transition: 0.4s all ease;
  @media only screen and (max-width: 1200px) {
    flex-basis: 48.5%;
  }
  @media only screen and (max-width: 480px) {
    flex-basis: 100%;
  }
  &:hover {
    &:before {
      height: 0;
    }
    color: #ffffff;
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    z-index: 1;
    transition: 0.4s all ease;
  }
}

.tiles__caption {
  z-index: 2;
}

// end Tiles
// form elements
.form {
  @include rfs(20);
  .form-group {
    margin-bottom: 2em;
  }
  .form-control {
    border-radius: 0;
    height: 3em;
    min-height: 38px;
    border: solid 1px #3e2353;
  }
  textarea.form-control {
    height: 320px;
  }
  input,
  textarea {
    &::placeholder {
      color: #3e2353;
      font-size: 1.2em;
    }
  }
}

// map
.map-canvas {
  width: 100%;
  height: 300px;
  min-height: 300px;
}

// Contact
.contact {
  padding: 5.5vw 0 3vw;
  color: #3e2353;
  @media (min-width: 1200px) {
    .container {
      max-width: 1600px;
    }
    &__info {
      margin-top: -0.4em;
      padding-left: 4.6vw;
    }
    .form__submit {
      font-size: 1.5em;
    }
  }
  .form__submit {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.5em;
    margin-left: auto;
    padding: 0.5em 3em;
    font-size: 1em;
  }
  .form__notice {
    margin-top: 4em;
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 2em;
    line-height: 1.37;
    @media only screen and (max-width: 1920px) and (min-width: 1200px) {
      min-height: 40vw;
    }
    .map-canvas {
      margin-top: 1em;
      height: 25vw;
    }
  }
  @media (min-width: 768px) {
    .container {
      max-width: 960px;
    }
  }
}

.contact-success {
  display: none;
  padding: 10vw 0 10vw;
  .contr {
    max-width: 800px;
  }
  .content__title {
    font-family: $font-primary;
    margin-bottom: 0.2em;
  }
}

.walk-in {
  padding: 5.3vw 0;
  h4 {
    font-size: 1.66em;
    font-weight: 900;
    line-height: 1.03;
  }
  p {
    font-size: 1.25em;
  }
}

// industries
.industries__navigation {
  background-color: #e7e7e7;
  .content__column {
    flex-basis: 50%;
    @media only screen and (max-width: 768px) {
      flex-basis: 100%;
    }
  }
  .tiles-descr,
  .tiles-wrap {
    padding: 4em 3vw;
  }
  .tiles-descr {
    max-width: 700px;
    padding-left: 8.125vw;
    @media only screen and (max-width: 768px) {
      padding-bottom: 0;
      padding-left: 3vw;
    }
    .button {
      margin-top: 8vw;
    }
    .content__title {
      margin-bottom: 0.5em;
      font-family: $font-primary;
      &:before {
        height: 195%;
      }
    }
  }
}

// Masonry
.masonry {
  .content__column {
    flex-basis: 50%;
    min-height: 56.7vw;
    @media only screen and (max-width: 768px) {
      flex-basis: 62.5%;
      &.content__column-picture {
        flex-basis: 37.5%;
      }
    }
  }
  .content__row--inverse {
    .content__column-picture {
      order: 2;
    }
  }
  .bg-purple {
    color: #ffffff;
    &:before {
      background-color: #220f40;
    }
    .masonry__bg-caption {
      color: #392854;
    }
  }
  .bg-grey {
    &:before {
      background-color: #d4d0da;
    }
    .masonry__bg-caption {
      color: #dbd7e0;
    }
  }
  &__copy {
    position: relative;
    overflow: hidden;
    height: 100%;
    padding: 13.85vw 3vw 8.75vw 8.75vw;
    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: -1;
    }
    .content__title {
      margin-bottom: 1em;
    }
    p {
      margin-bottom: 1em;
    }
  }
  &__descr {
    max-width: 610px;
  }
  &__bg-caption {
    position: absolute;
    left: -1%;
    top: 13%;
    color: #f2f1f4;
    @include rfs(260);
    font-family: "Bebas Neue", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1;
    letter-spacing: 0.05em;
    z-index: -1;
  }
}

.content__title--avenir {
  font-family: $font-primary;
  font-weight: 900;
  @include rfs(40);
  line-height: 1.25;
  &:before {
    top: 9%;
  }
}

// Products
.products {
  padding: 6.5vw 0 0;
  .contr {
    padding: 0 3vw;
  }
  &__provide {
    .content__row {
      flex-wrap: nowrap;
    }
    .content__column {
      &:first-child {
        flex-basis: 62.6%;
      }
      &::last-child {}
    }
  }
  &__provide-right {
    max-width: 266px;
    .btn {
      margin-top: 2.5em;
    }
  }
  &__tabs {
    margin-top: 2.8em;
    .content__title {
      margin-bottom: 1em;
    }
  }
  .nav-tabs {
    max-width: 1580px;
    margin: 0 auto;
    padding: 0 1em;
    justify-content: space-between;
    color: #ffffff;
    @media only screen and (max-width: 992px) {
      flex-wrap: wrap;
    }
  }
  .nav-item {
    flex-basis: 24%;
    display: flex;
    align-items: center;
    padding: 1em;
    font-size: 1.25em;
    font-weight: 900;
    line-height: 1.13;
    border-radius: 0;
    border: none;
    background-color: #220f40;
    @media only screen and (max-width: 1366px) {
      font-size: 1em;
    }
    @media only screen and (max-width: 992px) {
      flex-basis: 48%;
      margin-bottom: 1em;
    }
    &:hover {
      color: #ffffff;
    }
    .products__icon {
      margin-right: 3vw;
    }
  }
  .nav-item.active {
    color: #ffffff;
    background-color: #ffa861;
  }
  .icon {
    fill: #ffffff;
    @include rfs(76);
  }
  .icon-bandz {
    @include rfs(56);
  }
  .tab-pane {
    &.active {
      background-color: #ffa861;
    }
    .content__row {
      justify-content: flex-start;
      flex-wrap: nowrap;
    }
    .content__column {
      padding: 6vw 0;
      &:first-child {
        flex-basis: 28%;
      }
      &:last-child {
        flex-basis: 60%;
        padding: 6vw 3vw;
      }
    }
    .products__icon {
      display: flex;
      justify-content: flex-end;
    }
    .icon {
      @include rfs(120);
    }
    .icon-bandz {
      @include rfs(100);
    }
    .content__title {
      color: #ffffff;
      &:before {
        background-color: #220f40;
      }
    }
    p {
      margin-bottom: 1em;
    }
    h6 {
      font-size: inherit;
    }
    ul.list {
      padding-left: 0;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    li.list-item {
      flex-basis: 48%;
      list-style-type: none;
      @media only screen and (max-width: 768px) {
        flex-basis: 100%;
      }
    }
  }
}